.todogroup{
    padding: 10px 0px;
    color: black;
    color: #fff;
}

.todoli{
    display: flex;
    justify-content: space-evenly;
    align-items: center;
}

.action{
    width: 86.4438px;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
}

@keyframes doneitem{
    to {  
      width:100%;
     }
}

.todocontent{
    position: relative;
    width: 70%;
    transition: opacity .2s linear;
}

.todoisdone{
    opacity: 0.6;
}

.todoisdone:before{
    content: '';
    display: block;
    width: 0%;
    position: absolute;
    border: 0.2px solid #ff0000;
    top: 50%;
    animation: doneitem .3s ease-out 0s forwards;
}


.todoicon{
    width: 24px;
    height: 24px;
    cursor: pointer;
}